


























































































































































































































































.wrapper {
	margin: 30px 5% 0 5%;
}
.date-picker {
	display: inline-flex;
}

.clear-date-picker {
	margin-top: 7px;
}

.items-per-page-picker {
	float: right;
	margin-left: 7px;
}

.md-layout {
	width: 100%;
	margin-bottom: 5px;
}

.md-layout-item {
	margin-right: 5px;
}

.grid {
	clear: both;
}

.card-wrapper {
	box-sizing: border-box;
	padding: 5px;
}

table {
	width: 100%;
}

#view-toggle {
	float: right;
	margin-top: 16px;
	.md-button {
		margin: 0;
	}
}

#search-input {
	float: left;
	width: calc(100% - 200px);
	margin-top: 16px;
	margin-bottom: 16px;
	font-size: 1.75em !important;
	#search-query-input {
		display: inline-block;
		width: 100%;
		max-width: 500px;
		padding: 0;
		margin: 0;
		margin-left: 5px;
		font-size: 1em;
		line-height: 1em;
		color: inherit;
		background: transparent;
		border: none;
		border-bottom: 1px solid grey;
		outline: none;
		&:focus {
			color: var(--md-theme-default-primary);
			border-bottom: 1px solid var(--md-theme-default-primary);
		}
	}
	#result-headline {
		display: block;
		font-size: 1rem;
	}
}
